body {margin:0;}

.navbar ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #2BA84A;
    position: fixed;
    top: 0;
    width: 100%;
}

.navbar li {
    float: left;
}

.navbar li a {
    display: block;
    color: #2D3A3A;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;

}

.navbar li a:hover {
    background-color: #248232;
}

.active {
    background-color: #9999A1;
}

